import React, { useState, useEffect } from 'react';
import axios from 'axios';

const LoginPage = ({ onLogin }) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');

    // Reset form on mount to prevent autofill issues
    useEffect(() => {
        setEmail('');
        setPassword('');
        setError('');
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        setError('');
        
        console.log('LoginPage: Login attempt:', { email, password });

        try {
            const response = await axios.post('/api/auth/login', {
                email,
                password
            }, {
                withCredentials: true
            });

            console.log('LoginPage: Login response:', response.data);

            if (response.data.success) {
                console.log('LoginPage: Login successful, calling onLogin with:', response.data.user);
                onLogin(response.data.user);
            } else {
                setError('Login fehlgeschlagen');
            }
        } catch (error) {
            console.error('LoginPage: Login error:', error);
            setError(error.response?.data?.error || 'Login fehlgeschlagen');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-gray-50 flex">
            {/* Left Side - Real Elevator Image */}
            <div className="hidden lg:flex lg:w-1/2 relative overflow-hidden">
                {/* Background Image */}
                <div 
                    className="absolute inset-0 bg-cover bg-center bg-no-repeat"
                    style={{
                        backgroundImage: 'url(/static/images/elevator.jpg)',
                        filter: 'brightness(0.7) contrast(1.1)'
                    }}
                ></div>
                
                {/* Overlay Gradient */}
                <div className="absolute inset-0 bg-gradient-to-br from-blue-600/40 to-purple-600/40 z-10"></div>
                <div className="absolute inset-0 bg-black/20 z-20"></div>
                
                {/* Content */}
                <div className="relative w-full h-full flex items-center justify-center z-30">
                    <div className="text-center text-white">
                        <div className="mb-8">
                            <div className="w-48 h-32 mx-auto mb-6 bg-white/20 backdrop-blur-sm rounded-lg flex items-center justify-center p-4">
                                <img 
                                    src="/static/images/lackner-logo.png" 
                                    alt="Lackner Aufzge" 
                                    className="max-w-full max-h-full object-contain"
                                />
                            </div>
                        </div>
                        <h1 className="text-5xl font-bold mb-8 text-shadow-lg">Lackner Aufzge</h1>
                        
                        {/* Decorative Elements */}
                        <div className="space-y-6">
                            <div className="flex justify-center space-x-3">
                                <div className="w-3 h-3 bg-white/70 rounded-full animate-pulse"></div>
                                <div className="w-3 h-3 bg-white/70 rounded-full animate-pulse" style={{animationDelay: '0.2s'}}></div>
                                <div className="w-3 h-3 bg-white/70 rounded-full animate-pulse" style={{animationDelay: '0.4s'}}></div>
                            </div>
                        </div>
                    </div>
                </div>
                
                {/* Additional Decorative Elements */}
                <div className="absolute top-8 left-8 w-16 h-16 border border-white/30 rounded-lg opacity-60"></div>
                <div className="absolute top-32 right-12 w-12 h-12 border border-white/30 rounded-full opacity-60"></div>
                <div className="absolute bottom-16 left-16 w-20 h-20 border border-white/30 transform rotate-45 opacity-60"></div>
                <div className="absolute bottom-32 right-8 w-10 h-10 border border-white/30 opacity-60"></div>
            </div>

            {/* Right Side - Login Form */}
            <div className="w-full lg:w-1/2 flex items-center justify-center px-4 sm:px-6 lg:px-8 py-12">
                <div className="max-w-md w-full space-y-8">
                    {/* Mobile Logo */}
                    <div className="lg:hidden text-center">
                        <div className="mx-auto h-20 w-32 bg-white rounded-lg flex items-center justify-center shadow-lg p-2">
                            <img 
                                src="/static/images/lackner-logo.png" 
                                alt="Lackner Aufzge" 
                                className="max-w-full max-h-full object-contain"
                            />
                        </div>
                        <h2 className="mt-4 text-2xl font-bold text-gray-900">Lackner Aufzge</h2>
                        <p className="mt-2 text-sm text-gray-600">Zeiterfassung System</p>
                    </div>

                    {/* Login Form */}
                    <div className="bg-white rounded-2xl shadow-xl border border-gray-200 p-8">
                        <div className="text-center mb-8">
                            <h2 className="text-2xl font-bold text-gray-900">Willkommen zurck</h2>
                            <p className="mt-2 text-sm text-gray-600">Melden Sie sich an, um fortzufahren</p>
                        </div>

                        <form className="space-y-6" onSubmit={handleSubmit}>
                            <div>
                                <label htmlFor="login-email" className="block text-sm font-medium text-gray-700 mb-2">
                                    E-Mail
                                </label>
                                <div className="relative">
                                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                        <svg className="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 12a4 4 0 10-8 0 4 4 0 008 0zm0 0v1.5a2.5 2.5 0 005 0V12a9 9 0 10-9 9m4.5-1.206a8.959 8.959 0 01-4.5 1.207" />
                                        </svg>
                                    </div>
                                    <input
                                        id="login-email"
                                        name="login-email"
                                        type="text"
                                        autoComplete="username"
                                        required
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        className="appearance-none relative block w-full pl-10 pr-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm transition-colors"
                                        placeholder="monteur"
                                    />
                                </div>
                            </div>

                            <div>
                                <label htmlFor="login-password" className="block text-sm font-medium text-gray-700 mb-2">
                                    Passwort
                                </label>
                                <div className="relative">
                                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                        <svg className="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                                        </svg>
                                    </div>
                                    <input
                                        id="login-password"
                                        name="login-password"
                                        type="password"
                                        autoComplete="current-password"
                                        required
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        className="appearance-none relative block w-full pl-10 pr-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm transition-colors"
                                        placeholder="monteur123"
                                    />
                                </div>
                            </div>

                            {error && (
                                <div className="bg-red-50 border border-red-200 rounded-lg p-4">
                                    <div className="flex">
                                        <div className="flex-shrink-0">
                                            <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                                                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                                            </svg>
                                        </div>
                                        <div className="ml-3">
                                            <p className="text-sm text-red-800">{error}</p>
                                        </div>
                                    </div>
                                </div>
                            )}

                            <div>
                                <button
                                    type="submit"
                                    disabled={loading}
                                    className="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-lg text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors shadow-lg"
                                >
                                    {loading ? (
                                        <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                        </svg>
                                    ) : (
                                        <svg className="-ml-1 mr-3 h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 16l-4-4m0 0l4-4m-4 4h14m-5 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h7a3 3 0 013 3v1" />
                                        </svg>
                                    )}
                                    {loading ? 'Anmelden...' : 'Anmelden'}
                                </button>
                            </div>
                        </form>

                        {/* Test Accounts */}
                        <div className="mt-8 pt-6 border-t border-gray-200">
                            <h3 className="text-sm font-medium text-gray-700 mb-4">Test-Accounts (Entwicklung)</h3>
                            <div className="space-y-2 text-xs">
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Monteur:</span>
                                    <span className="font-mono text-gray-900">monteur / monteur123</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Admin:</span>
                                    <span className="font-mono text-gray-900">admin / admin123</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Bro:</span>
                                    <span className="font-mono text-gray-900">buero / buero123</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Lohn:</span>
                                    <span className="font-mono text-gray-900">lohn / lohn123</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Footer */}
                    <div className="text-center">
                        <p className="text-xs text-gray-500">
                             2025 Lackner Aufzge GmbH. Alle Rechte vorbehalten.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;
