import React, { useState, useEffect } from 'react';
import axios from 'axios';
import LoginPage from './components/LoginPage';
import MonteurDashboard from './components/MonteurDashboard';

function App() {
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        checkAuthStatus();
    }, []);

    const checkAuthStatus = async () => {
        try {
            const response = await axios.get('/api/auth/me', {
                withCredentials: true
            });
            console.log('Auth check response:', response.data);
            if (response.data.authenticated) {
                setUser(response.data.user);
            } else {
                setUser(null);
            }
        } catch (error) {
            console.log('Nicht angemeldet:', error.message);
            setUser(null);
        } finally {
            setLoading(false);
        }
    };

    const handleLogin = (userData) => {
        console.log('App: Login erfolgreich, User-Daten:', userData);
        setUser(userData);
    };

    const handleLogout = async () => {
        console.log('App: Logout gestartet');
        try {
            await axios.post('/api/auth/logout', {}, {
                withCredentials: true
            });
        } catch (error) {
            console.error('Logout error:', error);
        } finally {
            console.log('App: User-State auf null gesetzt');
            setUser(null);
        }
    };

    console.log('App render - user:', user, 'loading:', loading);

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
                    <p className="mt-4 text-gray-600">Lade...</p>
                </div>
            </div>
        );
    }

    // Immer Login-Seite zeigen wenn kein User
    if (!user) {
        console.log('Rendering LoginPage - no user authenticated');
        return <LoginPage onLogin={handleLogin} />;
    }

    console.log('Rendering Dashboard for user:', user);

    // Render appropriate dashboard based on user role
    if (user.role === 'Monteur') {
        return <MonteurDashboard user={user} onLogout={handleLogout} />;
    }

    // Default dashboard for other roles
    return (
        <div className="min-h-screen bg-gray-50">
            <header className="bg-blue-600 shadow-lg">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex justify-between items-center py-4">
                        <div className="flex items-center space-x-4">
                            <div className="h-10 w-32 bg-white rounded-lg flex items-center justify-center p-2">
                                <img 
                                    src="/static/images/lackner-logo.png" 
                                    alt="Lackner Aufzge" 
                                    className="max-w-full max-h-full object-contain"
                                />
                            </div>
                            <div className="text-2xl font-bold text-white">Lackner Aufzge</div>
                        </div>
                        <div className="flex items-center space-x-4">
                            <span className="text-white text-sm">{user.name}</span>
                            <button
                                onClick={handleLogout}
                                className="bg-blue-700 hover:bg-blue-800 text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors"
                            >
                                Abmelden
                            </button>
                        </div>
                    </div>
                </div>
            </header>
            <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-8">
                    <h1 className="text-2xl font-bold text-gray-900 mb-4">
                        Willkommen, {user.name}!
                    </h1>
                    <p className="text-gray-600">
                        Dashboard fr {user.role} wird implementiert...
                    </p>
                </div>
            </main>
        </div>
    );
}

export default App;
