import React, { useState, useEffect } from 'react';
import axios from 'axios';

const ZeiterfassungPage = ({ user }) => {
    const [currentTime, setCurrentTime] = useState(new Date());
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [isWorking, setIsWorking] = useState(false);
    const [breakStatus, setBreakStatus] = useState('no_break');
    const [timeEntries, setTimeEntries] = useState([]);
    const [workSummary, setWorkSummary] = useState({
        total_hours: '0.00',
        regular_hours: '0.00',
        overtime_hours: '0.00',
        entries_count: 0
    });

    // Formular-State
    const [formData, setFormData] = useState({
        elevator_id: '',
        location: '',
        entry_date: new Date().toISOString().split('T')[0],
        activity_type: 'maintenance',
        other_activity: '',
        start_time: '',
        end_time: '',
        break_duration: '0:30',
        description: '',
        is_emergency: false,
        is_overtime: false
    });

    // Filter-State
    const [filters, setFilters] = useState({
        date_type: 'today',
        date_start: '',
        date_end: '',
        activity_type: '',
        show_advanced: false
    });

    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
        }, 1000);
        return () => clearInterval(timer);
    }, []);

    useEffect(() => {
        fetchCurrentStatus();
        fetchTimeEntries();
        fetchWorkSummary();
        setCurrentTimeValue();
    }, []);

    const formatTime = (date) => {
        return date.toLocaleTimeString('de-DE', { hour: '2-digit', minute: '2-digit' });
    };

    const setCurrentTimeValue = () => {
        const now = new Date();
        let hours = now.getHours().toString().padStart(2, '0');
        let minutes = Math.round(now.getMinutes() / 30) * 30;
        if (minutes === 60) {
            minutes = 0;
            hours = ((parseInt(hours) + 1) % 24).toString().padStart(2, '0');
        }
        const currentTime = `${hours}:${minutes.toString().padStart(2, '0')}`;
        setFormData(prev => ({ ...prev, start_time: currentTime }));
    };

    const fetchCurrentStatus = async () => {
        try {
            const response = await axios.get('/api/monteur/current-status', { withCredentials: true });
            setIsWorking(response.data.is_working);
            setBreakStatus(response.data.break_status);
        } catch (err) {
            console.error('Error fetching current status:', err);
        }
    };

    const fetchTimeEntries = async () => {
        try {
            const response = await axios.get('/api/monteur/time-entries', { withCredentials: true });
            setTimeEntries(response.data.time_entries);
        } catch (err) {
            console.error('Error fetching time entries:', err);
        }
    };

    const fetchWorkSummary = async () => {
        try {
            const response = await axios.get('/api/monteur/work-summary', { withCredentials: true });
            setWorkSummary(response.data);
        } catch (err) {
            console.error('Error fetching work summary:', err);
        }
    };

    const handleClockIn = async () => {
        setLoading(true);
        try {
            await axios.post('/api/monteur/clock-in', {}, { withCredentials: true });
            fetchCurrentStatus();
            fetchWorkSummary();
            fetchTimeEntries();
        } catch (err) {
            setError('Fehler beim Einstempeln.');
        } finally {
            setLoading(false);
        }
    };

    const handleClockOut = async () => {
        setLoading(true);
        try {
            await axios.post('/api/monteur/clock-out', {}, { withCredentials: true });
            fetchCurrentStatus();
            fetchWorkSummary();
            fetchTimeEntries();
        } catch (err) {
            setError('Fehler beim Ausstempeln.');
        } finally {
            setLoading(false);
        }
    };

    const handleStartBreak = async () => {
        try {
            await axios.post('/api/monteur/start-break', {}, { withCredentials: true });
            fetchCurrentStatus();
        } catch (err) {
            setError('Fehler beim Starten der Pause.');
        }
    };

    const handleEndBreak = async () => {
        try {
            await axios.post('/api/monteur/end-break', {}, { withCredentials: true });
            fetchCurrentStatus();
        } catch (err) {
            setError('Fehler beim Beenden der Pause.');
        }
    };

    const handleFormSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        try {
            // Hier wrde die API fr das Erstellen eines Zeiteintrags aufgerufen
            console.log('Zeiteintrag erstellen:', formData);
            // await axios.post('/api/monteur/time-entry', formData, { withCredentials: true });
            fetchTimeEntries();
            setFormData({
                elevator_id: '',
                location: '',
                entry_date: new Date().toISOString().split('T')[0],
                activity_type: 'maintenance',
                other_activity: '',
                start_time: '',
                end_time: '',
                break_duration: '0:30',
                description: '',
                is_emergency: false,
                is_overtime: false
            });
        } catch (err) {
            setError('Fehler beim Erstellen des Zeiteintrags.');
        } finally {
            setLoading(false);
        }
    };

    const handleInputChange = (e) => {
        const { name, value, type, checked } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: type === 'checkbox' ? checked : value
        }));
    };

    const activityLabels = {
        'maintenance': 'Wartung',
        'repairs': 'Reparatur',
        'fixedprice': 'Festpreis',
        'emergency': 'Notdienst',
        'office': 'Bro',
        'sick': 'Krank',
        'vacation': 'Urlaub',
        'holiday': 'Feiertag',
        'overtime': 'berstunden',
        'other': 'Sonstige'
    };

    const activityIcons = {
        'maintenance': '',
        'repairs': '',
        'fixedprice': '',
        'emergency': '',
        'office': '',
        'sick': '',
        'vacation': '',
        'holiday': '',
        'overtime': '',
        'other': ''
    };

    return (
        <div className="min-h-screen bg-gray-100">
            <header className="bg-blue-500 shadow-sm">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex justify-between items-center py-2">
                        <div className="flex items-center">
                            <div className="h-8 w-24 bg-white rounded flex items-center justify-center p-1">
                                <img src="/static/images/lackner-logo.png" alt="Lackner Aufzge" className="max-w-full max-h-full object-contain" />
                            </div>
                        </div>
                        <div className="flex items-center space-x-4 text-white text-sm">
                            <div className="flex items-center space-x-2">
                                <span className="text-xs text-blue-200">{currentTime.toLocaleDateString('de-DE')}</span>
                                <span className="text-xs font-medium">{formatTime(currentTime)}</span>
                            </div>
                            <span className="font-medium">{user?.name || "Benutzer"}</span>
                        </div>
                    </div>
                </div>
            </header>

            <main className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
                <h1 className="text-2xl font-bold text-blue-600 mb-6">Monteur Zeiterfassungssystem</h1>

                {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-4" role="alert">
                        {error}
                    </div>
                )}

                {/* Aktueller Status */}
                <div className="bg-white rounded-lg shadow-md p-6 mb-6">
                    <h2 className="text-xl font-bold text-blue-600 mb-4">Aktueller Status</h2>
                    <div className="flex items-center space-x-4">
                        <div className="text-lg">
                            Status: <span className={`font-bold ${isWorking ? 'text-green-600' : 'text-red-600'}`}>
                                {isWorking ? 'Eingestempelt' : 'Ausgestempelt'}
                            </span>
                        </div>
                        <div className="text-lg">
                            Pause: <span className={`font-bold ${breakStatus === 'on_break' ? 'text-yellow-600' : 'text-gray-600'}`}>
                                {breakStatus === 'on_break' ? 'In Pause' : 'Keine Pause'}
                            </span>
                        </div>
                        <div className="flex space-x-2">
                            {!isWorking ? (
                                <button 
                                    onClick={handleClockIn}
                                    disabled={loading}
                                    className="bg-green-600 hover:bg-green-700 text-white font-semibold px-4 py-2 rounded disabled:opacity-50"
                                >
                                    Einstempeln
                                </button>
                            ) : (
                                <button 
                                    onClick={handleClockOut}
                                    disabled={loading}
                                    className="bg-red-600 hover:bg-red-700 text-white font-semibold px-4 py-2 rounded disabled:opacity-50"
                                >
                                    Ausstempeln
                                </button>
                            )}
                            {isWorking && (
                                breakStatus === 'no_break' ? (
                                    <button 
                                        onClick={handleStartBreak}
                                        disabled={loading}
                                        className="bg-yellow-600 hover:bg-yellow-700 text-white font-semibold px-4 py-2 rounded disabled:opacity-50"
                                    >
                                        Pause Start
                                    </button>
                                ) : (
                                    <button 
                                        onClick={handleEndBreak}
                                        disabled={loading}
                                        className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-4 py-2 rounded disabled:opacity-50"
                                    >
                                        Pause Ende
                                    </button>
                                )
                            )}
                        </div>
                    </div>
                </div>

                {/* Zeiteintrag Formular */}
                <div className="bg-white rounded-lg shadow-md p-6 mb-6">
                    <h2 className="text-xl font-bold text-blue-600 mb-4">Zeiteintrag</h2>
                    <form onSubmit={handleFormSubmit} className="space-y-4">
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                            <div>
                                <label htmlFor="elevator_id" className="block text-sm font-medium text-gray-700 mb-1">
                                    Aufzug-ID*
                                </label>
                                <input
                                    type="text"
                                    id="elevator_id"
                                    name="elevator_id"
                                    required
                                    placeholder="Aufzug-ID eingeben"
                                    value={formData.elevator_id}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                            <div>
                                <label htmlFor="location" className="block text-sm font-medium text-gray-700 mb-1">
                                    Standort*
                                </label>
                                <input
                                    type="text"
                                    id="location"
                                    name="location"
                                    required
                                    placeholder="Standort eingeben (z. B. Berlin Hauptbahnhof)"
                                    value={formData.location}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                            <div>
                                <label htmlFor="entry_date" className="block text-sm font-medium text-gray-700 mb-1">
                                    Datum*
                                </label>
                                <input
                                    type="date"
                                    id="entry_date"
                                    name="entry_date"
                                    required
                                    value={formData.entry_date}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                        </div>

                        {/* Ttigkeitsart */}
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">Ttigkeitsart*</label>
                            <div className="grid grid-cols-2 md:grid-cols-5 gap-3">
                                {Object.entries(activityLabels).map(([key, label]) => (
                                    <div
                                        key={key}
                                        className={`border rounded-md p-3 cursor-pointer transition-all hover:transform hover:-translate-y-0.5 ${
                                            formData.activity_type === key ? 'border-blue-500 bg-blue-50' : 'border-gray-300'
                                        }`}
                                        onClick={() => setFormData(prev => ({ ...prev, activity_type: key }))}
                                    >
                                        <div className="flex items-center">
                                            <span className="text-lg mr-2">{activityIcons[key]}</span>
                                            <span className="text-sm">{label}</span>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Sonstige Ttigkeit */}
                        {formData.activity_type === 'other' && (
                            <div>
                                <label htmlFor="other_activity" className="block text-sm font-medium text-gray-700 mb-1">
                                    Sonstige Ttigkeit beschreiben*
                                </label>
                                <input
                                    type="text"
                                    id="other_activity"
                                    name="other_activity"
                                    required
                                    placeholder="Ttigkeit beschreiben"
                                    value={formData.other_activity}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                        )}

                        {/* Zeiten */}
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                            <div>
                                <label htmlFor="start_time" className="block text-sm font-medium text-gray-700 mb-1">
                                    Startzeit*
                                </label>
                                <input
                                    type="time"
                                    id="start_time"
                                    name="start_time"
                                    required
                                    value={formData.start_time}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                            <div>
                                <label htmlFor="end_time" className="block text-sm font-medium text-gray-700 mb-1">
                                    Endzeit*
                                </label>
                                <input
                                    type="time"
                                    id="end_time"
                                    name="end_time"
                                    required
                                    value={formData.end_time}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                />
                            </div>
                            <div>
                                <label htmlFor="break_duration" className="block text-sm font-medium text-gray-700 mb-1">
                                    Pausenzeit
                                </label>
                                <select
                                    id="break_duration"
                                    name="break_duration"
                                    value={formData.break_duration}
                                    onChange={handleInputChange}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                                >
                                    <option value="0:00">Keine Pause</option>
                                    <option value="0:15">15 Minuten</option>
                                    <option value="0:30">30 Minuten</option>
                                    <option value="0:45">45 Minuten</option>
                                    <option value="1:00">1 Stunde</option>
                                </select>
                            </div>
                        </div>

                        {/* Beschreibung */}
                        <div>
                            <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
                                Beschreibung
                            </label>
                            <textarea
                                id="description"
                                name="description"
                                rows="3"
                                placeholder="Ttigkeit beschreiben..."
                                value={formData.description}
                                onChange={handleInputChange}
                                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:border-blue-500 focus:outline-none"
                            />
                        </div>

                        {/* Checkboxen */}
                        <div className="flex space-x-6">
                            <label className="flex items-center">
                                <input
                                    type="checkbox"
                                    name="is_emergency"
                                    checked={formData.is_emergency}
                                    onChange={handleInputChange}
                                    className="mr-2"
                                />
                                <span className="text-sm text-gray-700">Notdienst</span>
                            </label>
                            <label className="flex items-center">
                                <input
                                    type="checkbox"
                                    name="is_overtime"
                                    checked={formData.is_overtime}
                                    onChange={handleInputChange}
                                    className="mr-2"
                                />
                                <span className="text-sm text-gray-700">berstunden</span>
                            </label>
                        </div>

                        {/* Submit Button */}
                        <div className="flex justify-end">
                            <button
                                type="submit"
                                disabled={loading}
                                className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded disabled:opacity-50"
                            >
                                {loading ? 'Speichern...' : 'Zeiteintrag speichern'}
                            </button>
                        </div>
                    </form>
                </div>

                {/* Zeiteintrge bersicht */}
                <div className="bg-white rounded-lg shadow-md p-6">
                    <h2 className="text-xl font-bold text-blue-600 mb-4">Zeiteintrge bersicht</h2>
                    
                    {/* Filter */}
                    <div className="mb-4">
                        <button
                            onClick={() => setFilters(prev => ({ ...prev, show_advanced: !prev.show_advanced }))}
                            className="text-blue-600 hover:text-blue-800 text-sm"
                        >
                            {filters.show_advanced ? 'Erweitert ausblenden' : 'Erweitert anzeigen'}
                        </button>
                        
                        {filters.show_advanced && (
                            <div className="mt-4 grid grid-cols-1 md:grid-cols-4 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">Zeitraum</label>
                                    <select
                                        value={filters.date_type}
                                        onChange={(e) => setFilters(prev => ({ ...prev, date_type: e.target.value }))}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md"
                                    >
                                        <option value="today">Heute</option>
                                        <option value="week">Diese Woche</option>
                                        <option value="month">Dieser Monat</option>
                                        <option value="custom">Benutzerdefiniert</option>
                                    </select>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">Ttigkeit</label>
                                    <select
                                        value={filters.activity_type}
                                        onChange={(e) => setFilters(prev => ({ ...prev, activity_type: e.target.value }))}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-md"
                                    >
                                        <option value="">Alle</option>
                                        {Object.entries(activityLabels).map(([key, label]) => (
                                            <option key={key} value={key}>{label}</option>
                                        ))}
                                    </select>
                                </div>
                            </div>
                        )}
                    </div>

                    {/* Tabelle */}
                    <div className="overflow-x-auto">
                        <table className="min-w-full bg-white">
                            <thead>
                                <tr>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Datum</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Aufzug-ID</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Standort</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Ttigkeit</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Start/Ende</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Pause</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Dauer</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Status</th>
                                    <th className="py-2 px-4 border-b text-left text-sm font-semibold text-gray-600">Aktionen</th>
                                </tr>
                            </thead>
                            <tbody>
                                {timeEntries.map((entry) => (
                                    <tr key={entry.id}>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">{entry.date}</td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">{entry.elevator_id || '-'}</td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">{entry.location || '-'}</td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">
                                            <span className="mr-1">{activityIcons[entry.activity_type] || ''}</span>
                                            {activityLabels[entry.activity_type] || entry.activity_type}
                                        </td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">
                                            {entry.clock_in} - {entry.clock_out || 'luft'}
                                        </td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">
                                            {entry.break_start ? `${entry.break_start} - ${entry.break_end || 'luft'}` : '-'}
                                        </td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">{entry.total_hours}h</td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">
                                            <span className={`px-2 py-1 rounded text-xs ${
                                                entry.status === 'completed' ? 'bg-green-100 text-green-800' :
                                                entry.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                                                'bg-blue-100 text-blue-800'
                                            }`}>
                                                {entry.status}
                                            </span>
                                        </td>
                                        <td className="py-2 px-4 border-b text-sm text-gray-700">
                                            <button className="text-blue-600 hover:text-blue-800 mr-2">Bearbeiten</button>
                                            <button className="text-red-600 hover:text-red-800">Lschen</button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>

                    {/* Zusammenfassung */}
                    <div className="mt-6 p-4 bg-gray-50 rounded-lg">
                        <h3 className="text-lg font-semibold text-gray-800 mb-2">Wochenbersicht</h3>
                        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 text-sm">
                            <div>
                                <span className="font-medium">Gesamtstunden:</span> {workSummary.total_hours}h
                            </div>
                            <div>
                                <span className="font-medium">Regulre Stunden:</span> {workSummary.regular_hours}h
                            </div>
                            <div>
                                <span className="font-medium">berstunden:</span> {workSummary.overtime_hours}h
                            </div>
                            <div>
                                <span className="font-medium">Eintrge:</span> {workSummary.entries_count}
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    );
};

export default ZeiterfassungPage;
